@import "normalize";

$bgc: #E1D4C9;
$dark: #403F3D;
$accent: #B0907A;
$border-light: #C1B6AD;
$border-dark: #665F55;
body{
  background-color: $bgc;
  max-width: 1440px;
  padding: 20px 0 40px;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin: auto;
  font-family: Inter, sans-serif;
}
%links-and-buttons-text{
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  color: $dark;
}
h1 {
  font-size: 72px;
  font-weight: 600;
  line-height: 105%;
}
h2{

}
p{
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}
.accent_italic{
  font-style: italic;
  color: $accent;
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-items: center;
  padding: 0 40px;
  .header__line{
    transition: 0.3s ease-in;
    height: 2px;
    width: 100%;
    background-color: $dark;
    transform: scale(0);
  }
  .header__menu-item:hover .header__line{
    transform: scale(1);
  }
  .header__link{
    display: flex;
    gap: 8px;
    @extend %links-and-buttons-text;
  }
  .header__nav{
    display: flex;
    gap:  40px;
  }
  .header__menu-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
  }
}
.enjoy{
  background-image: url('../../pic/main/enjoy.jpg');
  padding: 100px;
  .enjoy__offer{
    max-width: 530px;
    gap: 40px;
    & h1{color: $bgc};
  }
}